.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
  color: #606266;
  gap: 4rem;
  height: fit-content;
  max-width: 1312px;
  position: relative;
  margin: 0 auto;

}

.footer>div {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.fragment {
  display: flex ;
  justify-content: space-between;
  gap: 40px;
}

.fragment_box {
  max-width: 438px;
  padding: 4rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center
  }
  
.footer_box {
  padding: 4rem 1rem;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: space-between;
  font-size: 12px;
}
.footer_box p {
  color: #191a239f;

} 



@media not all and (min-width: 1024px) {
  .footer>div>div {
    flex-direction: column;
    align-items: center;
  }

  .footer>div>div>div {
    align-self: flex-start;
  }
}
.paragraph {
  display: none; /* corresponde a hidden */
  font-size: 0.75rem; /* corresponde a text-xs-light */
  font-weight: 300; /* assuming "light" means light weight */
  color: #1f2937; /* corresponde a text-neutral-800 */
}

@media (min-width: 1024px) {
  .paragraph {
    display: block; /* corresponde a lg:block */
  }
}
.paragraph_2 {
  display: block; /* corresponde a block */
  font-size: 0.75rem; /* corresponde a text-xs-light */
  font-weight: 300; /* assuming "light" means light weight */
  color: #1f2937; /* corresponde a text-neutral-800 */
}

@media (min-width: 1024px) {
  .paragraph_2 {
    display: none; /* corresponde a lg:hidden */
  }
}
