.social_link {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 16px;
}

.social_link>div {
  display: flex;
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.logo_container {
  display: flex;
  gap: 4px;
  height: 18px;
}

.logo_container img:nth-child(1){
  width: 18px;
}
.logo_container img:nth-child(2){
  width: 128px;
}