.fomr {
  width: 100%;
  font-size: 14px;
}
.container {
  display: flex;
  border: 1px solid #e5e7eb; /* corresponde a border-gray-200 */
  border-radius: 9999px; /* corresponde a rounded-full */

  padding: 8px;
  background: linear-gradient(344deg, #ffffff, var(--tw-gradient-stops));
}

.input {
  outline: none;
  width: 100%;
  border: none;
  background-color: transparent;
}

.button_1 {
  border: none;
  border-radius: 9999px; /* corresponde a rounded-full */
  background-color: #5865F2; /* corresponde a bg-[#5865F2] */
  color: white; /* corresponde a text-brand-white-constant */
  align-items: center; /* corresponde a items-center */
  justify-content: flex-end; /* corresponde a justify-end */
  padding-top: 6px; /* corresponde a py-[6px] */
  padding-bottom: 6px; /* corresponde a py-[6px] */
  padding-left: 24px; /* corresponde a px-[24px] */
  padding-right: 24px; /* corresponde a px-[24px] */
  display: none; /* corresponde a hidden */
}

.paragraph {
  padding: 0; /* corresponde a p-0 */
  margin: 0; /* corresponde a m-0 */
  font-family: 'Outfit', sans-serif; /* corresponde a font-Outfit */
  text-align: center; /* corresponde a text-center */
  font-size: 0.875rem; /* corresponde a text-sm */
  font-weight: 500; /* corresponde a font-medium */
  line-height: 1.25rem; /* corresponde a leading-5 */
}

@media (min-width: 1024px) {
  .button_1 {
    display: block; /* corresponde a lg:block */
  }
}

.button_2 {
  border-radius: 9999px; /* corresponde a rounded-full */
  border: none;
  color: white;
  background-color: #5865F2; /* corresponde a bg-[#5865F2] */
  align-items: center; /* corresponde a items-center */
  justify-content: center; /* corresponde a justify-center */
  padding-top: 6px; /* corresponde a py-[6px] */
  padding-bottom: 6px; /* corresponde a py-[6px] */
  padding-left: 24px; /* corresponde a px-[24px] */
  padding-right: 24px; /* corresponde a px-[24px] */
  display: block; /* corresponde a block */
  width: 100%; /* corresponde a w-full */
}

@media (min-width: 1024px) {
  .button_2 {
    display: none; /* corresponde a lg:hidden */
  }
}

