.nav {
  width: 100%;
  height: 100%;
  padding: 0 8px;
  background-color: white;

}

.ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  height: 100%;
  flex-grow: 1;
}

.ul_a {
  color: #2f3136;
  font-size: 14px;
  text-decoration: none;
  height: 100%;
  display: grid;
  padding: 12px 16px;
  place-content: center;
  cursor: pointer;
  height: 100%;
}

.container_Submenu {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  cursor: pointer;
  height: 100%;
}

.container_Submenu:hover > svg {
  rotate: 180deg;
}