.soon {
  font-size: 10px;
  line-height: 1;
  color: #F97316;
  background-color: #e6e7ed;
  border-radius: 9999px;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  height: fit-content;
  width: fit-content;
}

@media (prefers-contrast: more) {
  .contrast-more\:bg-brand-dark {
    background-color: var(--color-dark);
  }
}