.gradientContainer {
  border-radius: 1rem; /* corresponde a rounded-2xl */
  padding: 1px; /* corresponde a p-[1px] */
  padding-right: 0; /* corresponde a pr-0 */
  background: linear-gradient(344deg, #ffffff, var(--tw-gradient-stops));
}

.gradientContainer.dark {
  background: linear-gradient(344deg, rgba(159, 70, 221, 0.4), var(--tw-gradient-stops)); /* dark:from-purplish-400/40 */
}

.container {
  gap: 1rem; /* corresponde a gap-4 */
}

.heading {
  /* font-weight: 600; corresponde a font-semibold */
  font-size: 1.25rem; /* corresponde a text-xl */
  line-height: 1.75rem; /* corresponde a leading-7 */
  align-self: stretch; /* corresponde a self-stretch */
  color: black; /* corresponde a text-brand-dynamic-primary */
}

.paragraph {
  font-size: 0.875rem; /* corresponde a text-sm */
  color: #525252; /* corresponde a text-neutral-600 */
}



@media (max-width: 640px) {
  .container {
    text-align: center; /* corresponde a max-sm:text-center */
  }
}
