.link {
  position: relative;
  color: #2f3136;
  height: 100%;
}

.link:hover {
  color: #5865f1;
}

.link_button {
  display: flex;
  border: none;
  align-items: center;
  background-color: white;
  height: 100%;
}

.link_button_div {
  margin-right: 4px;
  text-align: right;
  overflow: hidden;
  /* text-overflow: ellipsis; */
  /* white-space: nowrap; */
}

.link_button_div_span {
  font-size:14px;
  font-weight: 300;
}

.link_button_div_span:hover {
  color: #5865f1;
}

.nav_sub_menu {
  transform-origin: left;
  transition-duration: 100ms;
  transition: all;
  right: -118%;
  position: absolute;
  z-index: 10;
  background-color: white;
  border-radius: 8px;
  top: 30px;
  border-radius: 12px;
  border: 1px solid ;
  border-color: rgb(157 159 163 / 0.2);
  overflow: hidden;
}

.nav_sub_menu_div_div {
  border-style: solid;
  border-bottom: solid 14px  white ;
  border-left: solid 14px transparent ;
  border-right: solid 14px transparent ;
}

@media (max-width: 1100px) {
  .link {
    display: none;
  }

  .link_button_div_span {
    display: none;
  }
}