.nav_link {
  width: 100%;
  max-width: 746px;
  flex-grow: 1;
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 2.5rem;
  color: #4b4d68;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.nav_link_box {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.nav_link_box>a:nth-child(1){
  font-weight: 600;
  color: #323346e1;
}
.nav_link_box>a {
  font-size:14px;
  line-height: 150%;
  text-decoration: none;
}

.nav_link_box a {
  color: #323346d5;
  text-decoration: none;
}

.fragment {
  display: flex;
}

.fragment>a {
  position: relative;
  text-decoration: none;
}

.fragment>a:hover span {
  width: 100%;
}

.fragment>a>span {
  position: absolute;
  bottom: -1px;
  left: 0px;
  width: 0px;
  height: 1px;
  background-color: #5865f1;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.fragment_soon {
  display: flex;
  height: fit-content;
  align-items: center;
  gap: 0.5rem;
  
  position: relative;
}
.fragment_soon a {
  font-size: 14px;
}


@media (min-width: 640px) {
  .nav_link {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}